@keyframes tailWag {
  0% { transform: rotateZ(0deg); }
  25% { transform: rotateZ(-8deg); }
  75% { transform: rotateZ(8deg); }
  100% { transform: rotateZ(0deg); }
}

@keyframes fishFloat {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-3px); }
}

@keyframes eyeBlink {
  0%, 85%, 100% { 
    transform: scaleY(1);
    opacity: 1;
  }
  90%, 95% { 
    transform: scaleY(0.1);
    opacity: 0.8;
  }
}

.fish {
  left: 50px !important;
  transform: scale(0.8);
  animation: fishFloat 2s ease-in-out infinite;
}

.fish-tail {
  animation: tailWag 1s ease-in-out infinite;
}

.fish-eye {
  animation: eyeBlink 3s ease-in-out infinite;
  transform-origin: center;
}

@keyframes wave {
  0% { 
    transform: translateX(0) scaleY(1); 
    opacity: 0.8;
  }
  50% { 
    transform: translateX(-50px) scaleY(1.2); 
    opacity: 1;
  }
  100% { 
    transform: translateX(-100px) scaleY(1); 
    opacity: 0.8;
  }
}

.water-wave-2 {
  animation: wave 2s linear infinite reverse !important;
}

.surface-wave {
  animation: wave 1s linear infinite !important;
  z-index: 1;
}

.fish.jump {
  animation: jump 0.8s ease-out;
}

@keyframes jump {
  0% { bottom: 80px; }
  50% { bottom: 220px; }
  100% { bottom: 80px; }
}

.monster-fish {
  transform: scale(0.8);
  position: absolute;
  bottom: 80px;
  right: -80px;
  width: 75px;
  height: 45px;
  animation: moveLeft linear infinite;
  z-index: 5;
}

.monster-body {
  width: 60px;
  height: 38px;
  background: linear-gradient(135deg, #FF4500 0%, #8B0000 50%, #000 100%);
  border-radius: 40% 60% 60% 40% / 50% 50% 50% 50%;
  position: relative;
  border: 2px solid #000;
  box-shadow: inset 0 2px 4px rgba(255, 0, 0, 0.3);
}

.monster-tail {
  position: absolute;
  right: -8px;
  top: 6px;
  width: 0;
  height: 0;
  border-right: 25px solid #8B0000;
  border-top: 13px solid transparent;
  border-bottom: 13px solid transparent;
  filter: drop-shadow(2px 2px 2px rgba(0,0,0,0.3));
}

.monster-eye {
  position: absolute;
  top: 8px;
  left: 8px;
  width: 12px;
  height: 12px;
  background: radial-gradient(circle, #ff0000 30%, #cc0000 70%, #800000 100%);
  border-radius: 50%;
  border: 2px solid #000;
  box-shadow: inset 1px 1px 2px rgba(255,255,255,0.3);
}

.monster-eye::after {
  content: '';
  position: absolute;
  top: 2px;
  left: 1px;
  width: 4px;
  height: 4px;
  background: #000;
  border-radius: 50%;
}

.monster-teeth {
  position: absolute;
  top: 23px;
  left: 0px;
  width: 13px;
  height: 8px;
  background: repeating-linear-gradient(90deg, 
    #fff 0, 
    #fff 1.5px, 
    transparent 1.5px, 
    transparent 3px
  );
  clip-path: polygon(0% 0%, 100% 0%, 95% 100%, 85% 0%, 75% 100%, 65% 0%, 55% 100%, 45% 0%, 35% 100%, 25% 0%, 15% 100%, 5% 0%, 0% 0%);
}

/* Add scales effect */
.monster-body::before {
  content: '';
  position: absolute;
  top: 5px;
  left: 5px;
  width: 50px;
  height: 28px;
  background-image: 
    radial-gradient(circle at 20% 80%, #333 1px, transparent 1px),
    radial-gradient(circle at 80% 20%, #333 1px, transparent 1px),
    radial-gradient(circle at 40% 40%, #333 1px, transparent 1px);
  background-size: 8px 8px, 6px 6px, 10px 10px;
  background-position: 0 0, 4px 4px, 2px 6px;
  opacity: 0.4;
  border-radius: inherit;
}

/* Add dorsal fin to monster */
.monster-body::after {
  content: '';
  position: absolute;
  top: -15px;
  left: 15px;
  width: 25px;
  height: 13px;
  background: linear-gradient(135deg, #8B0000 0%, #A00000 100%);
  border-radius: 80% 50% 20% 20%;
  border: 1px solid #660000;
  transform: rotate(-10deg);
  filter: drop-shadow(1px 1px 3px rgba(0,0,0,0.4));
  clip-path: polygon(0% 100%, 30% 0%, 100% 20%, 80% 100%);
}

.boat-obstacle {
  position: absolute;
  bottom: 160px !important;
  right: -100px;
  width: 80px;
  height: 90px;
  animation: moveLeft linear infinite;
  z-index: 15;
}

.boat-body {
  width: 70px;
  height: 25px;
  background: #8B4513;
  border-radius: 0 0 35px 35px;
  position: relative;
  border: 2px solid #000;
}

.boat-sail {
  position: absolute;
  left: 25px;
  top: -35px;
  width: 0;
  height: 0;
  border-left: 20px solid #fff;
  border-top: 15px solid transparent;
  border-bottom: 15px solid transparent;
  border-right: 2px solid #000;
}

.net {
  position: absolute;
  top: 25px;
  left: 10px;
  width: 50px;
  height: 60px;
  background-image: 
    linear-gradient(0deg, #666 1px, transparent 1px),
    linear-gradient(90deg, #666 1px, transparent 1px);
  background-size: 8px 8px;
  opacity: 0.7;
}

@keyframes moveLeft {
  from { 
    right: -100px;
    transform: translateX(0);
  }
  to { 
    right: calc(100% + 100px);
    transform: translateX(0);
  }
}

.start-btn:hover {
  background: #1976D2 !important;
}

.retry-btn:hover {
  background: #45a049 !important;
}

/* Cloud styling: arrange three cloud sizes across two vertical rows and let them pass over the sun */
.clouds {
  z-index: 4;
  pointer-events: none;
}

.cloud {
  position: absolute;
  display: flex;
  align-items: flex-end;
  gap: -10px;
  animation: cloudMove linear infinite;
  will-change: transform;
}

.cloud .puff {
  border-radius: 50%;
  display: inline-block;
  background: radial-gradient(circle at 30% 30%, #ffffff 0%, #f2f2f2 60%, #e0e0e0 100%);
  box-shadow: 0 4px 6px rgba(0,0,0,0.08);
}

/* size variations for different cloud groups */
.cloud-small  .puff { width: 22px; height: 22px; }
.cloud-medium .puff { width: 32px; height: 32px; }
.cloud-large  .puff { width: 46px; height: 46px; }

/* depth effect using opacity and subtle blur */
.cloud-small  { opacity: 1; }
.cloud-medium { opacity: 0.9; filter: blur(0.2px); }
.cloud-large  { opacity: 0.8; filter: blur(0.4px); }

/* movement of clouds across the screen */
@keyframes cloudMove {
  0% { transform: translateX(0); }
  100% { transform: translateX(calc(100% + 900px)); }
}

@media (max-width: 768px) {
  .game-container {
    height: 75vh !important; /* 60vh থেকে 75vh */
    min-height: 460px !important; /* 300px থেকে 450px */
    max-height: 600px !important; /* 400px থেকে 600px */
    margin: 10px !important;
    touch-action: manipulation !important;
  }
  
  .score {
    font-size: 16px !important;
    padding: 4px 8px !important;
    top: 10px !important;
    right: 10px !important;
  }
  
  .boat-obstacle {
    bottom: 140px !important;
    transform: scale(0.7) !important;
  }
  
  .monster-fish {
    transform: scale(0.6) !important;
  }
  
  .fish {
    transform: scale(0.65) !important;
    left: 40px !important;
  }
}